import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom';

// 
import Home from './views/Home';
// 
import Course from './views/Course';
// 
import Find from './views/Find';
// 
import Mine from './views/Mine';
// 
import Detail from './views/Detail';
// 
import Search from './views/Search';
// 
import FindDetail from './views/FindDetail';
// 
import MineVideo from './views/mine/MineVideo';
// 
import MineLevel from './views/mine/MineLevel';
// 
import MineHis from './views/mine/MineHis';
// 
import MinePrice from './views/mine/MinePrice';
// 
import UploadVideo from './views/mine/UploadVideo';

import './assets/css/style.css';

class App extends Component {
  render() {
    return (
      <div className="App">
        
        <div className="app-content">
          {/* views */}
          <Switch>
            <Route exact path="/" component={Home}/>
            <Route exact path="/course" component={Course}/>
            <Route exact path="/find" component={Find}/>
            <Route exact path="/mine" component={Mine}/>
            <Route exact path="/detail" component={Detail}/>
            <Route exact path="/search" component={Search}/>
            <Route exact path="/fdetail" component={FindDetail}/>
            <Route exact path="/minevideo" component={MineVideo}/>
            <Route exact path="/minelevel" component={MineLevel}/>
            <Route exact path="/minehis" component={MineHis} />
            <Route exact path="/mineprice" component={MinePrice} />
            <Route exact path="/upload" component={UploadVideo} />
          </Switch>
        </div>
       
      </div>
    );
  }
}

export default App;



