import React, { Component } from 'react'; 
import Tabbar from '../components/tabbar';
import Login from '../components/login';
import Register from '../components/register';

import { Button, WingBlank,WhiteSpace, Grid, Modal, Toast } from 'antd-mobile';
import header from '../assets/img/header.jpg';

const prompt = Modal.prompt;

class Mine extends Component{ 
    componentDidMount(){
        let islogin;
        (localStorage.islogin === '0') ? islogin = true : islogin = false;
        this.setState({
            islogin: islogin,
            user: localStorage.user
        })
    }
    state = {
        islogin: false,
        login: true,
        user:'',
        griddata:[{
            icon: require('../assets/img/faxian_topic.png'),
            text:'',
            href: '#/minevideo'
        },{
            icon: require('../assets/img/faxian_teacher.png'),
            text:'',
            href: '#/minelevel'
        },{
            icon: require('../assets/img/faxian_mall.png'),
            text:'',
            href: '#/mineprice'
        },{
            icon: require('../assets/img/faxian_lineclass.png'),
            text:'',
            href: '#/minehis'
        }]
    }
    goReg = () => {
        console.log('goreg')
        this.setState({
            login: false
        })
    }
    goLogin = () =>{
        console.log('gologin')
        this.setState({
            login: true
        })
    }
    success = (user) => {
        localStorage.islogin = '0';
        localStorage.user = user;
        this.setState({
            islogin: true,
            user: user
        })
    }
    logout = () => {
        localStorage.islogin = '1';
        this.setState({
            islogin: false
        })
    } 
    clickGrid = (item) => {
        console.log(item)
        window.location = item.href;
    }
    render(){
        const {islogin, login, griddata} = this.state; 
        return (
            <div className="Mine">
                {/* Header */}
                {/* <Header /> */}
                {/* / */}
                <div className={!islogin ? "menu-show":"menu-hide"}>
                    <div className={login ? "menu-show":"menu-hide"}>
                        <Login goReg={()=>this.goReg} success={(user)=>this.success(user)} />
                    </div>
                    <div className={!login ? "menu-show":"menu-hide"}>
                        <Register goLogin={()=>this.goLogin} success={(user)=>this.success(user)} />
                    </div>
                </div>
                {/*  */}
                <div className={islogin ? "menu-show":"menu-hide"}>
                    <div className="mine-box">
                        <div className="fl header-pic">
                            <img src={header} alt="" width="100%"/>
                        </div>
                        <div className="fl header-info">
                            <h2>{this.state.user}</h2>
                            <small className="lightgrey"> 500.00 </small>
                        </div>
                        <div className="fl recharge" onClick={ () => prompt(
                            '',
                            '',
                            [
                                { text: '' },
                                { text: '', 
                                    onPress: price =>  {
                                        Toast.loading('...',1)
                                        setTimeout(() => {
                                            Toast.success('',.5)
                                        },1000)
                                }
                            }],
                            'default',
                        )}>
                            
                        </div>
                    </div>
                    <Grid data={griddata} activeStyle={false} hasLine={false} onClick={(item,index)=>this.clickGrid(item,index)} />

                    {/*  */}
                    <WingBlank size="lg">
                        <WhiteSpace size="lg" />
                        <Button type="warning" onClick={()=>this.logout()} style={{fontSize:"14px"}}></Button>
                    </WingBlank>
                </div>
                {/* Tabbar */}
                <Tabbar selected="mine" />
            </div>
        ) 
    } 
} 
export default Mine;


