import React,{Component} from 'react';
import Header from '../components/header';

class Search extends Component{
    shap = () => {

    }
    gosearch = () => {
      window.location=`#/course`;
    }
    render(){
        return(
            <div className="search-bodyer">
                {/* Header */}
                <Header back/>
                <div className="goods-eac-box">
                    <div className="eac-per-ens">
                        <div className="aingstoit">
                            <input value="video" id="search_cate" type="hidden" disabled="" />
                        </div>
                        <input autoFocus={true} onInput={this.shap()} id="search_combo_input" />
                        <i className="icon icon-quxiao"></i>
                    </div>
                    <button id="btn_search_site" onClick={()=>this.gosearch()}></button>
                </div>

                <div className="headlines">
                    <h5></h5>
                    <ul id="video_list">
                        <li onClick={()=>window.location='#/detail?name='}></li>
                        <li onClick={()=>window.location='#/detail?name='}></li>
                        <li onClick={()=>window.location='#/detail?name='}></li>
                        <li onClick={()=>window.location='#/detail?name='}></li>
                        <li onClick={()=>window.location='#/detail?name='}></li>
                        <li onClick={()=>window.location='#/detail?name='}></li>
                    </ul>
                </div>
            </div>
        )
    }
}
export default Search


