import React,{Component} from 'react';

import Header from '../../components/header';

class MineLevel extends Component{
    state = {
       list:[{
        type: '1',
        core: '5',
        time: new Date().toLocaleString()
       },{
        type: '1',
        core: '5',
        time: new Date().toLocaleString()
       },{
        type: '1',
        core: '5',
        time: new Date().toLocaleString()
       },{
        type: '1',
        core: '5',
        time: new Date().toLocaleString()
       },{
        type: '1',
        core: '5',
        time: new Date().toLocaleString()
       },{
        type: '1',
        core: '5',
        time: new Date().toLocaleString()
       },{
        type: '1',
        core: '5',
        time: new Date().toLocaleString()
       },{
        type: '1',
        core: '5',
        time: new Date().toLocaleString()
       },{
        type: '1',
        core: '5',
        time: new Date().toLocaleString()
       },{
        type: '1',
        core: '5',
        time: new Date().toLocaleString()
       },{
        type: '1',
        core: '5',
        time: new Date().toLocaleString()
       },{
        type: '1',
        core: '5',
        time: new Date().toLocaleString()
       }]
    }
    getlist = () => {
        return(
            this.state.list.map((item,index) => 
                <li key={index}>
                    <span className={item.type==='1'?"menu-show text-red":"menu-hide"}></span> 
                    <span className={item.type==='-1'?"menu-show text-red":"menu-hide"}></span> 
                    <span className="text-blue"> {item.core} </span>
                    <small className="fr">{item.time}</small>
                </li>
            )
        )
    }
    render(){
        return (
            <div className="MineLevel">
                <Header back title="" />
                <div className="mine-level-box">
                    <div className="fl">
                        <h3></h3>
                        <div>
                            <img src={require('../../assets/img/v2.png')} alt="v1" height="40px" />
                        </div>
                    </div>
                    <div className="fl">
                        <h3></h3>
                        <div className="text-blue" style={{lineHeight:"40px"}}>200</div>
                    </div>
                    <div className="clear"></div>
                </div>
                <ul className="mine-level-list">
                    {this.getlist()}
                </ul>
            </div>
        )
    }
}
export default MineLevel;


