import React,{Component} from 'react';

class Register extends Component {
    state = {
        name: '',
        pwd: ''
    }
    check_regis = () => {
        console.log(this.state)
        this.props.success(this.state.name)
    }
    getname = (e) =>{
        this.setState({
            name: e.target.value
        })
    }
    getpwd = (e) =>{
        this.setState({
            pwd: e.target.value
        })
    }
    render(){
        return(
            <div className="loginbox">
                <span className="icon-colse" href="/"></span>
                <div className="login-hd">
                    <li className="on">
                        <span onClick={this.props.goLogin()}></span>
                    </li>
                    <li></li>
                    <li>
                        <span style={{color:'#000'}}></span>
                    </li>
                </div>
                <div className="login-bd">
                    <div className="item code">
                        <input type="text" name="login_email" placeholder="//" onChange={this.getname}/>
                    </div>
                    <div className="item">
                        <input name="login_password" type="password" className="inp" onChange={this.getpwd} autoComplete="off" placeholder="" style={{border:'1px solid #e6e6e6'}}
                        />
                    </div>
                </div>
                <span className="login_btn"  onClick={()=>this.check_regis()}></span>

                <div className="third-party">
                    <dl>
                        <dd>
                            <span className="icon-qzone"></span>
                        </dd>
                    </dl>
                </div>
            </div>
        )
    }
}
export default Register;


