import React, { Component } from 'react';
import { NavBar, Icon } from 'antd-mobile';

import userImg from '../assets/img/user.png';
import logo from '../assets/img/logo.png';

class Header extends Component {
  componentDidMount(){
  }
  state = {
  }
  goUser = () => {
    window.location="#/mine";
  }
  goBack = () => {
    window.history.go(-1);
  }
  goHome = () => {
    window.location = '#/';
  }
  goSearch = () => {
    window.location = '#/search';
  }
  render() {
    const {title} = this.props;
    return (
      <div className="Header fixed-top">
           <NavBar
            mode="light"
            leftContent={[
                <img src={logo} alt="logo" style={{width:"2.2rem"}} 
                  key="logo" className={(!this.props.back)?"menu-show":"menu-hide"}
                  onClick={()=>this.goHome()} />,

                <Icon key="0" type="left" style={{ marginRight: '16px' }} 
                  className={(this.props.back)? "menu-show":"menu-hide"}
                  onClick={()=>this.goBack() }
                />
            ]}
            rightContent={[
                <Icon key="0" type="search" style={{ marginRight: '16px' }}  onClick={()=>this.goSearch()}/>,
                <img key="user" src={userImg} alt="" onClick={()=>this.goUser()}/>
            ]}
            style={{ height: "55px" }}
            >
            {title}
            </NavBar>
           
      </div>
    );
  }
}

export default Header;


