import React, { Component } from 'react';
import { Tabs, Accordion, List, Modal, Toast } from 'antd-mobile';

import bg from '../assets/img/detail-bg.jpg';
import Header from '../components/header';
import BtnBottom from '../components/btnbotm';
class Detail extends Component{
    componentDidMount(){
        let param =decodeURI(window.location.hash.split('?')[1]).split('&'),
        paramObj = {},
        videobg = '',
        purchase = false;

        for(let i = 0; i<param.length; i++){
            let key = param[i].split('=')[0],
            value = param[i].split('=')[1];
            paramObj[key] = value;
        }
        //
        (paramObj.src)? videobg = paramObj.src : videobg = bg;
        (paramObj.purchase) ? purchase = true : purchase = false;

        
        this.setState({
            item: paramObj,
            videobg: videobg,
            purchase: purchase
        })
    }
    state = {
        item: {},
        purchase: false,
        videobg:bg,
        tabs: [{ 
            title:""
        }, { 
            title: ""
        },{ 
            title: ""
        }],
        showVideo: false
    }
    onChange = (key) => {
        console.log(key);
    }
    btnCLick = () => {
        if(!this.state.purchase){
            const alert = Modal.alert;
            alert('', `${this.state.item.price} `, 
            [{ 
                    text: '', 
                    onPress: () => console.log('cancel') 
                }, { 
                    text: '', 
                    onPress: () => {
                        Toast.loading('...',1)
                        setTimeout(() => {
                            Toast.success('',.5);
                            this.setState({
                                showVideo: true
                            })
                        },1000) 
                    }
                }]
            )
        }else{
            this.setState({
                showVideo: true
            })
        }
       
    }
    render(){
        const {item,tabs,videobg,showVideo} = this.state;
        return (
            <div className="Detail">
                {/* Header */}
                <Header back title={item.name}/>
                <div className="detail-header"  style={{background:`url(${videobg}) no-repeat `,backgroundSize:"cover"}}>
                    <div className={showVideo?"menu-hide":"menu-show detail-msg"}>
                        <h2 className="text-center">{item.name}</h2>
                        <p>
                            {item.desc}
                        </p>
                    </div>
                    <div className={showVideo?"menu-hide":"menu-show cover"}></div>
                    
                    <div className={!showVideo?"menu-hide":"menu-show detail-cover"}>
                        <video src={require("../assets/H.mp4")} width="100%" controls poster=""></video>
                    </div>
                </div>
                {/* tabs */}
                <Tabs tabs={tabs}
                    initialPage={0}
                    onChange={(tab, index) => { console.log('onChange', index, tab); }}
                    onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}
                    >
                    <div style={{ padding:'20px', backgroundColor: '#fff' }}>
                        {/* <h3></h3> */}
                        <h2>{item.name}</h2>
                        <div>
                            {item.desc}
                        </div>
                        <small>
                            {item.price} |
                            {item.num}
                        </small>
                        <h3></h3>
                        <div>
                             <br/>
                            1. :  <br/>
                            2. : <br/>
                            3. AI: AI.
                        </div>
                    </div>
                    <div style={{ padding:'20px', backgroundColor: '#fff' }}>
                        <h3></h3>
                        <Accordion defaultActiveKey="0" className="my-accordion" onChange={this.onChange}>
                            <Accordion.Panel header="">
                                <List className="my-list">
                                    <List.Item></List.Item>
                                    <List.Item></List.Item>
                                    <List.Item></List.Item>
                                </List>
                            </Accordion.Panel>
                            <Accordion.Panel header="" className="pad">
                            <List className="my-list">
                                    <List.Item></List.Item>
                                    <List.Item></List.Item>
                                    <List.Item></List.Item>
                                </List>
                            </Accordion.Panel>
                            <Accordion.Panel header="" className="pad">
                            <List className="my-list">
                                    <List.Item></List.Item>
                                    <List.Item></List.Item>
                                    <List.Item></List.Item>
                                </List>
                            </Accordion.Panel>
                        </Accordion>
                    </div>
                    <div style={{ padding:'20px', backgroundColor: '#fff' }}>
                        <h3></h3>
                        <p></p>
                    </div>
                </Tabs>
                <div  className={showVideo?"menu-hide":"menu-show"} >
                    <BtnBottom click={()=>this.btnCLick} title=""/>
                </div>
            </div>
        )
    }
}

export default Detail;


