import React,{Component} from 'react';

import banner1 from '../../assets/img/banner1.jpg';
import banner2 from '../../assets/img/banner2.jpg';
import banner3 from '../../assets/img/banner3.jpg';

import Header from '../../components/header';

class MineHis extends Component{
    state = {
        courses:[{
            name: '',
            desc: ',',
            price: '20.00',
            num: '1000',
            color: '#0070bb',
            src: banner2
        },{
            name: '',
            desc: 'IT',
            price: '32.00',
            num: '1240',
            color:'#ff4545',
            src: banner3
        },{
            name: '',
            desc: 'IT',
            price: '23.00',
            num: '300',
            color:'#ff4545',
            src: banner1
        },{
            name: '',
            desc: '',
            price: '40.00',
            num: '2000',
            color:'#ff4545',
            src: banner2
        },{
            name: '',
            desc: ',',
            price: '20.00',
            num: '1000',
            color: '#0070bb',
            src: banner2
        },{
            name: '',
            desc: 'IT',
            price: '32.00',
            num: '1240',
            color:'#ff4545',
            src: banner3
        },{
            name: '',
            desc: 'IT',
            price: '23.00',
            num: '300',
            color:'#ff4545',
            src: banner1
        },{
            name: '',
            desc: '',
            price: '40.00',
            num: '2000',
            color:'#ff4545',
            src: banner2
        }]
    }
    getCourse = () => {
        return(
            this.state.courses.map((item,index) => 
             <div key={index} onClick={()=>this.goDetail(item)} className="paddLR">
                <div className="fl" style={{width:"38%",marginRight:"2%"}}>
                    <img src={item.src} alt="peitu" width="100%" style={{borderRadius:"10px",margin:"5% 0 0" }}/>
                </div>
                <div key={index}  className="fl text-l" style={{color:"#000",width:"60%"}} >
                    <div>{item.name}</div>
                    <small className="lineH-low">{item.desc}</small>
                    <div>
                        <small>{new Date().toLocaleString()}</small>
                    </div>
                </div>
                <div className="clear" />
             </div>
            )
        )
    }
    goDetail = (item) => {
        let hrefStr = [];
        for(let i in item){
          hrefStr.push(`${i}=${item[i]}`) 
        }
        window.location=`#/detail?${encodeURI(hrefStr.join('&'))}`;
    }
    render(){
        return (
            <div className="MineHis">
                <Header back title="" />
                 {/*  */}
                 <div className="course">
                    <div className="course-content">
                        {this.getCourse()} 
                    </div>
                </div> 
            </div>
        )
    }
}
export default MineHis;


