import React,{Component} from 'react';
import {Button} from 'antd-mobile';

class BtnBottom extends Component{
    render(){
        return(
            <div className="btn-bottom fixed-bottom">
                <Button type="warning" style={{height:"50px",fontSize: "14px"}} onClick={this.props.click()}>{this.props.title}</Button>
            </div>
        )
    }
}
export default BtnBottom;


