import React, { Component } from 'react';
import Header from '../components/header';
import Tabbar from '../components/tabbar';
class Find extends Component{
    state = {
        lists: [{
            title: "",
            icon: require('../assets/img/faxian_lineclass.png'),
            children:[{
                title: '',
                text: '801960'
            },{
                title: '',
                text: ''
            },{
                title: '',
                text: '   '
            }]
        },{
            title: "",
            icon: require('../assets/img/faxian_exam.png'),
            children: [{
                title:'',
                text: ' '
            },{
                title:'',
                text:'SVAC'
            },{
                title: '',
                text:'[]'
            }]
        },{
            title: "",
            icon: require('../assets/img/faxian_teacher.png'),
            children:[{
                title: '',
                text: 'GYBSJX01-HHD ...'
            },{
                title:'',
                text:''
            },{
                title:'',
                text:'ZD-CA3...'
            }]
        },{
            title: "",
            icon: require('../assets/img/faxian_library.png'),
            children:[{
                title:'',
                text:'100201'
            },{
                title:'',
                text:'..'
            },{
                title:'',
                text:'ICR500G'
            }]
        },{
            title: "",
            icon: require('../assets/img/faxian_topic.png'),
            children:[{
                title:'',
                text:''
            },{
                title:'',
                text:'2005921OTI498'
            },{
                title:'',
                text:'1996'
            }]
        },{
            title: "",
            icon: require('../assets/img/faxian_group.png'),
            children:[{
                title:'',
                text:'() '
            },{
                title:'',
                text:'CSP'
            },{
                title:'',
                text:'1987.'
            }]
        }]
    }
    goFind = (item) => {
        let hrefStr = [];
        for(let i=0;i<item.children.length;i++){
            for(let j in item.children[i]){
              hrefStr.push(`${j}=${item.children[i][j]}`) 
            }
        }
        console.log(hrefStr)
        window.location=`#/fdetail?title=${item.title}&children=${encodeURI(JSON.stringify(item.children))}`;
    }
    getList = () => {
        return (
            this.state.lists.map((item,index) => 
                <li key={index} onClick={()=>this.goFind(item)}>
                    <img src={item.icon} alt={item.title} />
                    {item.title}
                    <i className="icon icon-youjiantou"></i>
                </li>
            )
        )
    }
    render(){
        return (
            <div className="Find">
                {/* Header */}
                <Header />
                <ul className="more_list">
                    {this.getList()}            
                </ul>
                {/* Tabbar */}
                <Tabbar selected="find" />
            </div>
        )
    }
}

export default Find;


