import React,{Component} from 'react';
import {Tabs} from 'antd-mobile';

import banner1 from '../../assets/img/banner1.jpg';
import banner2 from '../../assets/img/banner2.jpg';
import banner3 from '../../assets/img/banner3.jpg';

import Header from '../../components/header';
import BtnBottom from '../../components/btnbotm';

class MineVideo extends Component{
    state = {
        tabs:[ { 
            title: '', 
            sub: '1' 
        }, { 
            title: '', 
            sub: '2' 
        }],
        courses:[{
            name: '',
            desc: ',',
            price: '20.00',
            num: '1000',
            color: '#0070bb',
            src: banner2
        },{
            name: '',
            desc: 'IT',
            price: '32.00',
            num: '1240',
            color:'#ff4545',
            src: banner3
        },{
            name: '',
            desc: 'IT',
            price: '23.00',
            num: '300',
            color:'#ff4545',
            src: banner1
        },{
            name: '',
            desc: '',
            price: '40.00',
            num: '2000',
            color:'#ff4545',
            src: banner2
        },{
            name: '',
            desc: 'IT',
            price: '32.00',
            num: '1240',
            color:'#ff4545',
            src: banner3
        },{
            name: '',
            desc: 'IT',
            price: '23.00',
            num: '300',
            color:'#ff4545',
            src: banner1
        },{
            name: '',
            desc: '',
            price: '40.00',
            num: '2000',
            color:'#ff4545',
            src: banner2
        }]
    }
    getCourse = () => {
        return(
            this.state.courses.map((item,index) => 
             <div key={index} onClick={()=>this.goDetail(item)} className="paddLR">
                <div className="fl" style={{width:"38%",marginRight:"2%"}}>
                    <img src={item.src} alt="peitu" width="100%" style={{borderRadius:"10px",margin:"5% 0 0" }}/>
                </div>
                <div key={index}  className="fl text-l" style={{color:"#000",width:"60%"}} >
                    <div>{item.name}</div>
                    <small className="lineH-low">{item.desc}</small>
                    <div>
                        <small></small>
                    </div>
                </div>
                <div className="clear" />
             </div>
            )
        )
    }
    goDetail = (item) => {
        let hrefStr = [];
        for(let i in item){
          hrefStr.push(`${i}=${item[i]}`) 
        }
        window.location=`#/detail?purchase=0&${encodeURI(hrefStr.join('&'))}`;
    }
    btnCLick = () => {
        console.log('')
        window.location = '#/upload'
    }
    render(){
        const {tabs} = this.state; 
        return (
            <div className="MineVideo">
                <Header back title="" />
                <Tabs tabs={tabs}
                    initialPage={0}
                    onChange={(tab, index) => { console.log('onChange', index, tab); }}
                    onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}
                    >
                    {/*  */}
                    <div className="course">
                        <div className="course-content">
                            {this.getCourse()} 
                        </div>
                    </div>                 
                </Tabs>
                 
                <BtnBottom click={()=>this.btnCLick} title="" />
            </div>
        )
    }
}
export default MineVideo;


