import React,{Component} from 'react';
import { ImagePicker,Toast } from 'antd-mobile';
import Header from '../../components/header';
import BtnBottom from '../../components/btnbotm';

class UploadVideo extends Component{
    state = {
        files:[]
    }
    onChange = (files, type, index) => {
        console.log(files, type, index);
        this.setState({
          files,
        });
    }
    btnCLick = () => {
        Toast.loading('..',2);
        setTimeout(()=>{
            Toast.success('');
            this.setState({
                files:[]
            })
        },2000)
    }
    render(){
        const {files} = this.state;
        return(
            <div className="UploadVideo">
                <Header back title="" />
                <div className="paddTB paddLR white mine-price-box" style={{background:`url(${require('../../assets/img/record-banner.jpg')})  no-repeat`,backgroundSize:"cover"}}>
                    <big></big>
                </div>
                <ImagePicker
                    files={files}
                    onChange={this.onChange}
                    onImageClick={(index, fs) => console.log(index, fs)}
                    selectable={files.length < 5}
                    />
                <BtnBottom click={()=>this.btnCLick} title="" />
            </div> 
        )
    }
}
export default UploadVideo;


