import React,{Component} from 'react';
import { Flex, Modal, Toast } from 'antd-mobile';
import Header from '../../components/header';
import BtnBottom from '../../components/btnbotm';

const prompt = Modal.prompt;

class MinePrice extends Component{
    state = {
        lists:[{
            time: new Date().toLocaleString(),
            desc: "",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "",
            coin: "5"
        }]
    }
    getList = () =>{
        return(
            this.state.lists.map((item,index) => 
            <Flex className="text-center table-item" key={index}>
                <Flex.Item>
                    <small className="lineH-low">{item.time}</small>
                </Flex.Item>
                <Flex.Item>
                    <small className="lineH-low">{item.desc}</small>
                </Flex.Item>
                <Flex.Item>
                    <small className="lineH-low">{item.coin}</small>
                </Flex.Item>
            </Flex>
            )
        )
    } 
    btnCLick = () =>{
       prompt(
            '',
            '500.00,500',
            [
                { text: '' },
                { text: '', 
                    onPress: price =>  {
                        Toast.loading('...',1)
                        setTimeout(() => {
                            Toast.success('',.5)
                        },1000)
                    }
                }],
                'default',
            )
    }
    render(){
        return(
            <div className="MinePrice">
                <Header back title="" />
                <div className="paddTB paddLR white mine-price-box" style={{background:`url(${require('../../assets/img/record-banner.jpg')})  no-repeat`,backgroundSize:"cover"}}>
                    <img src={require('../../assets/img/coin.png')} alt="coin" width="15%" />
                    <big> 500.00 </big>
                </div>
                <div className="price-table">
                    <Flex className="text-center table-item">
                        <Flex.Item></Flex.Item>
                        <Flex.Item></Flex.Item>
                        <Flex.Item></Flex.Item>
                    </Flex>
                    {this.getList()}
                </div>
                <BtnBottom click={()=>this.btnCLick} title="" />
            </div>   
        )
    }
} 
export default MinePrice;


