import React, {Component} from 'react';
import {Tabs} from 'antd-mobile';
import Header from '../components/header';

class FindDetail extends Component{
    componentDidMount(){
        let param =decodeURI(window.location.hash.split('?')[1]).split('&'),
        paramObj = {};

        for(let i = 0; i<param.length; i++){
            let key = param[i].split('=')[0],
            value = param[i].split('=')[1];
            paramObj[key] = value;
        }
        let obj = [];
        try{
            obj = JSON.parse(paramObj.children)
        }catch(err){
            obj = paramObj.children
        }
        console.log(paramObj,obj)

        this.setState({
            tabs: obj,
            title: paramObj.title
        })
    }
    state = {
        title: '',
        tabs: [{}]
    }
    getContent = () => {
        return (
            this.state.tabs.map((item,index) => 
            <div key={index} style={{ padding: "20px", backgroundColor: '#fff',minHeight:'50vh' }}>
                {item.text}   
            </div>
            )
        )
    }
    render(){
        const {tabs,title} = this.state;
        return(
            <div className="FindDetail">
                <Header back title={title}/>
                <Tabs tabs={tabs}
                    initialPage={0}
                    onChange={(tab, index) => { console.log('onChange', index, tab); }}
                    onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}
                    >
                    {this.getContent()}                    
                </Tabs>
            </div>  
        )
    }
}
export default FindDetail


