import React, { Component } from 'react';
import { TabBar } from 'antd-mobile';
import home from '../assets/img/home.svg';
import homeActive from '../assets/img/home_active.svg';

import course from '../assets/img/course.svg';
import courseActive from '../assets/img/course_active.svg';

import find from '../assets/img/find.svg';
import findActive from '../assets/img/find_active.svg';

import mine from '../assets/img/mine.svg';
import mineActive from '../assets/img/mine_active.svg';

class Tabbar extends Component {
  state = {
    hidden: false,
  }
  render() {
    return (
      <div className="menu-show Tabbar fixed-bottom">
          <TabBar
            unselectedTintColor="#949494"
            tintColor="#33A3F4"
            barTintColor="white"
            hidden={this.state.hidden}
            >
            <TabBar.Item
                title=""
                key="home"
                icon={
                    <div style={{
                    width: '22px',
                    height: '22px',
                    background: 'url('+ home +') center center /  21px 21px no-repeat' }}
                    />
                }
                selectedIcon={<div style={{
                width: '22px',
                height: '22px',
                background: 'url('+ homeActive +')  center center /  21px 21px no-repeat' }}
                />
                }
                selected={this.props.selected === 'home'}
                onPress={() => {
                    window.location = '#/';
                }}
            />
            <TabBar.Item
                icon={
                <div style={{
                    width: '22px',
                    height: '22px',
                    background: 'url('+ course +') center center /  21px 21px no-repeat' }}
                />
                }
                selectedIcon={
                <div style={{
                    width: '22px',
                    height: '22px',
                    background: 'url('+ courseActive +') center center /  21px 21px no-repeat' }}
                />
                }
                title=""
                key="course"
                // badge={'new'}
                selected={this.props.selected === 'course'}
                onPress={() => {
                    window.location = '#/course';
                }}
            />
          <TabBar.Item
                icon={
                <div style={{
                    width: '22px',
                    height: '22px',
                    background: 'url('+ find +') center center /  21px 21px no-repeat' }}
                />
                }
                selectedIcon={
                <div style={{
                    width: '22px',
                    height: '22px',
                    background: 'url('+ findActive +') center center /  21px 21px no-repeat' }}
                />
                }
                title=""
                key="find"
                selected={this.props.selected === 'find'}
                onPress={() => {
                    window.location = '#/find';
                }}
           />
          <TabBar.Item
                icon={
                <div style={{
                    width: '22px',
                    height: '22px',
                    background: 'url('+ mine +') center center /  21px 21px no-repeat' }}
                />
                }
                selectedIcon={
                <div style={{
                    width: '22px',
                    height: '22px',
                    background: 'url('+ mineActive +') center center /  21px 21px no-repeat' }}
                />
                }
                title=""
                key="mine"
                selected={this.props.selected === 'mine'}
                onPress={() => {
                    window.location = '#/mine';
                }}
            />
    </TabBar>

      </div>
    );
  }
}

export default Tabbar;


